.mainHeading {
  margin-bottom: 60px;
}
.cpivalue,
.spivalue {
  font-family: monospace;
  font-size: x-large;
}
.creditTotFind {
  font-size: 16px;
  font-style: italic;
}
.main1-wrapper {
  text-align: center;
  position: relative;
}
.creditTotalPingala {
  z-index: 10;
  background: #000000e0;
  padding: 20px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  position: absolute;
  top: 10vh;
  animation: slideDown 0.3s ease-in-out;
  left: 25vw;
  border-radius: 16px;
  box-shadow: 2px 2px 10px black;
}
@keyframes slideDown {
  0% {
    opacity: 0;
    top: -30vh;
  }
  100% {
    opacity: 1;
    top: 10vh;
  }
}
.creditTotalCalc {
  border-radius: 16px;
  width: 60vw;
  left: 20vw;
  z-index: 10;
  background: #000000e0;
  padding: 20px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  position: absolute;
  top: 10vh;
  animation: slideDown 0.3s ease-in-out;
  box-shadow: 2px 2px 10px black;
}
@media screen and (max-width: 500px) {
  .creditTotalCalc {
    width: 100%;
    left: 0px;
  }
  .creditTotalPingala {
    left: 0px;
  }
}
